.Commodity {
    height: 30px;
    width: 30px;
    border-radius: 50%;
    background: purple;
}

.Commodity[name=bread]::before {
    content: "🍞"; /* bread emoji */
}

.Commodity[name=corn]::before {
    content: "🌽";
}

.Commodity[name=brick]::before {
    content: "🧱";
}

.Commodity[name=soap]::before {
    content: "🧼";
}