
  .Notes-section {
    max-width: 800px;
  }
  .Notes-section p,ol {
    
    text-align: left;
    
  }

  .Notes-section > section {
    margin-top: 3em;
  }

  .Equation-multiline {
    text-align: left;
    
  }

  .Equation-multiline > div {
    margin-top: 1em;
    margin-left: 2em;
  }

  .Equation-multiline > div > span {
    margin-right: 2em;
  }
  
  .Notes-section li {
    margin-left: 50px;
    margin-right: 50px;
  }
  
  .Notes-link {
    color: #61dafb;
  }
  
  .Notes-formula {
    font-style: italic;
    font-family: serif;
    font-size: 100%;
  }
  
  .Notes-term {
    font-style: italic;
  }