.Plotting {
    
    padding: 2em;

}

.Plotting-params {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.Plotting-inputs, .Plotting-labels {
    display: flex;
    flex-direction: column;
    height: 8em;
    width: 40%;
    justify-content: space-between;
}


.Plotting-labels {
    align-items: flex-start;
    padding-left: 5%;
}

.Plotting-inputs input[type=number] {
    width: 4em;
    align-self: flex-end;
    
}


.Plotting-results {
    display: flex;
    flex-direction: row;

}

.Plotting-equations {
    display: flex;
    flex-direction: column;
}